.item-enter {
	opacity: 0;
	transform: translateX(30px);
}
.item-enter-active {
	transform: translateX(0px);
	opacity: 1;
	transition: all 500ms ease-in;
}
.item-exit {
	opacity: 1;
	transform: translateX(0px);
}
.item-exit-active {
	opacity: 0;
	transform: translateX(30px);
	transition: all 500ms ease-in;
}


.alert-enter{
	opacity: 0;
	transform: translateY(-100%);
}

.alert-enter-active{
	opacity: 1;
	transform: translateY(0);
	transition: all 300ms cubic-bezier(.07,.77,.12,.95);
}

.alert-exit{
	opacity: 1;
}
.alert-exit-active{
	opacity: 0;
	transform: translateY(-100%);
	transition: all 300ms cubic-bezier(.07,.77,.12,.95);
}